const STORAGE_KEY = 'questionnaire';

export default {
    // 获取
    get(key = STORAGE_KEY) {

        return JSON.parse(window.localStorage.getItem(key));
    },
    // 存储
    save(items, key = STORAGE_KEY) {
        window.localStorage.setItem(key, JSON.stringify(items));
    }
}


